/** @jsx jsx */
import { jsx, css } from '@emotion/core'
import FilledStarSVG from 'public/icons/filled-star.svg?sprite'
import EmptyStarSVG from 'public/icons/empty-star.svg?sprite'

type Props = {
  /** The title of the platform */
  title: string
  /** The category of the platofrm */
  category: string
  /** A short description of the platform */
  description: string
  /** The quantity of starts */
  quantityStars: number
  /** The url background image of the plaform */
  backgroundImageUrl: string
}

export const FeaturedPlatform: React.FC<Props> = ({
  title,
  category,
  description,
  quantityStars,
  backgroundImageUrl,
}) => {
  const filledStars = Array.from(Array(quantityStars).keys())
  const emptyStars = Array.from(Array(5 - quantityStars).keys())

  return (
    <article
      css={css`
        padding: 2.4rem;
        border-radius: var(--radius);
        background-color: rgba(0, 0, 0, 0.16);
      `}
    >
      <header
        css={css`
          display: flex;
          flex-direction: column;
        `}
      >
        <h3
          css={css`
            font-size: 3.2rem;
            font-weight: 600;
            margin-bottom: 0.4rem;
          `}
        >
          {title}
        </h3>

        <div
          css={css`
            display: flex;
            justify-content: space-between;
          `}
        >
          <span
            css={css`
              color: rgba(255, 255, 255, 0.7);
              font-size: 1.8rem;
            `}
          >
            {category}
          </span>

          <ul
            css={css`
              display: grid;
              grid-auto-flow: column;
              grid-gap: 0.4rem;

              svg {
                width: 20px;
                height: 20px;
              }
            `}
          >
            {filledStars.map(filledStar => (
              <li key={`filled-star-${filledStar}`}>
                <FilledStarSVG />
              </li>
            ))}
            {emptyStars.map(emptyStar => (
              <li key={`empty-star-${emptyStar}`}>
                <EmptyStarSVG />
              </li>
            ))}
          </ul>
        </div>
      </header>

      <div
        css={css`
          border-radius: var(--radius);
          overflow: hidden;
          max-height: 294px;
          margin: 2.4rem 0;
        `}
      >
        <img
          css={css`
            width: 100%;
            height: 100%;
            border-radius: var(--radius);
          `}
          src={backgroundImageUrl}
        />
      </div>

      <footer
        css={css`
          color: #c0b3cc;
          line-height: 2.4rem;
        `}
      >
        {description}
      </footer>
    </article>
  )
}
